.course-table {

    .wp-block-column {

        display: flex;
        flex-direction: column;
        background: $light-grey;
        padding: $padding-base;
        border-radius: 8px;
        margin-bottom: $margin-base;

        p { margin-bottom: auto; }

        .wp-block-buttons {
            margin-top: $margin-base;

            .wp-block-button__link {

                background: $red;

                &:hover {
                    background: $dark-red;
                }
            }
        }
        
    }
}