html {
  
  box-sizing: content-box;
}

header {
  padding: 0;
  width: 100%;
  min-height: 42px;
}

.wrapper {
  width: 100%;
  display: grid;
  grid-template-columns: auto minmax(auto, $max-site-width - 750) minmax(auto, 750px) auto;
  grid-template-areas: ". sitecontent sidebar .";
  padding: 0 $padding-base;
  margin-bottom: $margin-base;
}

.home-content {
  width: 100%;
  display: grid;
  grid-template-columns: auto minmax(auto, $max-site-width/3) minmax(auto, $max-site-width/3) minmax(auto, $max-site-width/3)  auto;
  grid-template-areas: ". home-one home-two home-three ."
  ". home-four home-five home-six .";
  
  row-gap: 10px;
  margin-bottom: $margin-base;

  @media (max-width:768px) {
    grid-template-columns: auto minmax(auto, 1fr) minmax(auto, 1fr)  auto;
    grid-template-areas: ". home-one home-two ."
    ". home-three home-four ."
  ". home-five home-six .";
  }

  @media (max-width:630px) {
    grid-template-columns: auto minmax(auto, 1fr) auto;
    grid-template-areas: ". home-one ."
    ".  home-two ."
    ".  home-three ."
    ".  home-four ."
  ". home-five .";
  }

  h2 {
    color: $white;
    text-transform: uppercase;
    font-size: $font-lrg;
    font-weight: 800;
  }

  a {
    color: $white;
  }
}

.top-image {
  margin-bottom: $margin-base;
}

.main-container {
  max-width: $max-site-width;
  margin: 0 auto;
  padding: 0 $padding-base;
}

.wrapper-header {
  display: grid;
  grid-template-areas: "mainlogo nav";
  grid-template-columns: 200px auto;
  margin: ($margin-base * 2) 0;

  .main-logo {
    grid-area: mainlogo;
    width: 320px;
  }

  @media (max-width:920px) {

    .main-logo {
      grid-area: mainlogo;
      width: 200px;
    }
  }

  nav {
    grid-area: nav;
    align-self: center;
    justify-self: end;
  }
}

.page-content, .post-list, .page-title {
  grid-area: sitecontent;
  grid-column: 2/4;
  padding: 0 $padding-base;
}

.single-cpt_blog {

  
  .page-content {
    grid-area: sitecontent;

    &.page-title {
      grid-column: 2/4;
    }

  }
  .sidebar-blog {
    grid-area: sidebar;
    margin-left: $margin-base * 2;
  }
  @media (max-width:768px) {
    .sidebar-blog {
      margin-left: 0;
    }
  }
}

.home-testimonial {
  //grid-area: home-five;
  grid-column-start: home-five;
  grid-column-end: home-six;
  padding: $padding-base;
  background: $red;
  color: $white;
  border-radius: $border-radius-big;

  @media (max-width:768px) {
    grid-column-start: home-four;
    grid-column-end: home-four;
    margin-right: 10px;
  }

  blockquote {
    quotes: "“" "”" "‘" "’";
    position: relative;
    hanging-punctuation: first;
    padding-left: 20px;

    p {
      

      &::before {
        content: open-quote;
        font-size: 4rem;
        font-family: Arial, Helvetica, sans-serif;
        position: absolute;
          left: -.1em;
          top: 0.2em;

      }

      &::after {
        content: close-quote;
        font-size: 4rem;
        font-family: Arial, Helvetica, sans-serif;
        position: absolute;
        bottom: -0.2em;
        padding-left: 8px;
      }
    }
  }

  figcaption {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    .citation-text {
      text-align: right;
      margin-right: $margin-base / 2;

      strong {
        font-weight: 600;
        margin-bottom: $margin-base - 10;
        display: block;
      }
    }

    .citation-img {
      img {
        border-radius: 50%;
        width: 80px;
        border: 2px solid $white;
      }
    }
  }
  

}

.home-blog {
  grid-area: home-two;
  padding: $padding-base;
  background: $home-blue;
  color: $white;
  border-radius: $border-radius-big;
  margin-right: 10px;
}

.home-faqs {
  grid-area: home-three;
  padding: $padding-base;
  background: $purple;
  color: $white;
  border-radius: $border-radius-big;

  @media (max-width:768px) {
    margin-right: 10px;
  }
  
}

.home-projects {
  grid-area: home-one;
  grid-row-start: 1;
  grid-row-end: 3;
  padding: $padding-base;
  background: $home-green;
  color: $white;
  border-radius: $border-radius-big;
  margin-right: 10px;

  @media (max-width:768px) {
    grid-row-end: 2;
  }

}

footer {
 
  background-color: $white;

  .footer-top {
    display: grid;
    grid-template-columns: auto minmax(auto, $max-site-width /2) minmax(auto, $max-site-width /2) auto;
    grid-template-areas: ". footerSignup footerSocial .";
    padding: 20px;

    @media (max-width:630px) {
      grid-template-columns: auto minmax(auto, 1fr) auto;
    grid-template-areas: ". footerSignup ."
    ". footerSocial .";
    }

    .footer-signup {
      grid-area: footerSignup;
      padding: 0 $padding-base;
    }
    .footer-social {
      grid-area: footerSocial;
      justify-self: end;
      padding: 0 $padding-base;

      @media (max-width:630px) {
        justify-self: start;
        margin-top: $margin-base;
      }

      img {
        display: inline;
        width: 38px;
        height: auto;
      }


    }
  }

  .footer-lower {
    display: grid;
    grid-template-columns: auto minmax(auto, $max-site-width /2) minmax(auto, $max-site-width /2) auto;
    grid-template-areas: ". footerMenu footerCopyright .";
    padding: 20px;

    @media (max-width:630px) {
      grid-template-columns: auto minmax(auto, 1fr) auto;
    grid-template-areas: ". footerMenu ."
    ". footerCopyright .";
    }

    .footer-menu {
      grid-area: footerMenu;
      padding: 0 $padding-base;
    }
    .footer-copyright {
      grid-area: footerCopyright;
      text-align: right;
      padding: 0 $padding-base;

      @media (max-width:630px) {
        margin-top: $margin-base;
        text-align: left;
      }

      p {
        font-size: $font-xs;
        font-weight: 600;
      }
      
    }
  }
}

.copyright {
  grid-area: footerLow;
}

.footer-logo {
  grid-area: footerMid3;
  text-align: right;

  img {
    margin-left: auto;
    width: 100px;
    height: auto;
  }
}
.footer-left {grid-area: footerMid1;
  .social-media-links {

    margin-bottom: $margin-base /2;
    width: 100%;
    
    
  }

}




.footer-menu2 {
  grid-area: footerMid2;
}

@media (max-width:768px) {

  .wrapper-header {
    align-items: center;
    justify-items: center;
  }

  .wrapper {
    display: block;
    padding: 0;
  }

  .page-content {
    margin: 0 $margin-base;
    padding: 0;
  }
  

}

/** Mailchimp **/
.mail-list {
  h2 {
    color: $black;
  }

  .asterisk {
    color: $red;
  }

  .button {
    background: $red;
    color: $white;
    border: none;
    border-radius: 6px;
    padding: $padding-base;
    font-size: 16px;
  }

  .mail-fields {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: $padding-base 0;

    .mc-field-group {
      padding: 0 $padding-base;
      margin:  $margin-base;
      text-align: left;
      width: 25%;
      label {
        color: $red;
      }
      input {
        border: none;
        font-size: 16px;
        padding: $padding-base / 2 $padding-base;
        width: 100%;

      }

      .mce_inline_error {
        background: $red !important;
        text-align: center !important;
        margin-top: $margin-base / 2 !important;
      }

      
    }
  }

  .response {
    margin-bottom: $margin-base / 2 !important;
  }

  a {
    color: $red;
  }
}

@media (max-width:767px) {
  .mail-list {
    .mail-fields {
      flex-direction: column;

      .mc-field-group {
        width: 90%;
      }
    }
  }
}
