// Basic font sizes. Use Media queries to adapt them for screens.
$font-base: 16px;
$font-xs: $font-base - 4; // 12px
$font-sml: $font-base - 2; // 14px
$font-md: $font-base + 4; // h3 20px
$font-lrg: $font-base + 8; // h2 24px
$font-xl: $font-base * 2; // h1 32px

$line-height-base: 22px;
$line-height-xs: $line-height-base - 6; // 16px
$line-height-font-lrg: $line-height-base + 6; // 28px
$line-height-font-xl: $line-height-base + 14; // 36px

$font_bold: Arial;
$font_roman: Arial;
$font_light: Arial;

$font_weight: 600;
$font_weight_low: 200;

$heading-stack: 'Times New Roman', Arial, sans-serif, -apple-system, system-ui, BlinkMacSystemFont;
$body-stack: 'Open Sans', Arial, sans-serif, -apple-system, system-ui, BlinkMacSystemFont;

/* open-sans-300 - latin */
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 300;
  src: local(''),
       url('fonts/open-sans-v23-latin-300.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
       url('fonts/open-sans-v23-latin-300.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
  font-display: swap;
}
