body {
  font-family: $body-stack;
  font-size: $font-base;
  color: $black;
  background: $white;
}

h1 {
  font-size: $font-xl;
  margin-bottom: $margin-base;
  color: $black;
  font-weight: 700;
}

h2 {
  font-size: $font-lrg;
  margin-bottom: $margin-base;
  color: $red;
}

h3 {
  font-size: $font-md;
  margin-bottom: $margin-base;
  color: $red;
  line-height: normal;
}

h4 {
  font-size: $font-base;
  margin-bottom: $margin-base;
  color: $red;
}

p{
  font-size: $font-base;
  margin-bottom: $margin-base;
  line-height: $line-height-base;

  &.copyright {
    font-size: $font-xs;
    margin-bottom: 0;
  }
}

a {
  color: $red;

  &:hover {
    color: $black;
  }
}

.page-content ul {
  list-style-type: square;
  margin-left: $margin-base * 2;
  margin-bottom: $margin-base;

  li{
    margin-bottom: $margin-base / 2;
  }
}
